.footer__copyright {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  margin: 0;
  padding-bottom: 0;
}

@media screen and (max-width: 1024px) {
  .footer__copyright {
    font-size: 16px;
    line-height: 20px;
  }
}

@media screen and (max-width: 425px) {
  .footer__copyright {
    font-size: 14px;
    line-height: 17px;
  }
}
