.popup {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.1s, opacity 0.3s linear;
}

@media screen and (min-width: 1280px) {
  .popup {
    max-width: 1280px;
  }
}
